  text-transform: uppercase;
  height: 7.25em;
  max-width: 1110px;
  margin: 0 auto;
  padding-top: 4.125em;
  border-bottom: 1px solid #543080;
  letter-spacing: 0.1em;
  word-spacing: -0.375em;
}

.logo_header {
  font-size: 1.125rem;
  color: #e3d5ff;
