}

.wrap_links {
  position: relative;
  top: 2px;
}
.wrap_links a {
  display: inline-block;
  font-size: 0.875rem;
  text-decoration: none;
  color: #e3d5ff;
  padding-bottom: 0.75em;
  position: relative;
  overflow: hidden;
}
.wrap_links a::after {
  content: "";
  width: 1.1875em;
  display: block;
  border: 1px solid red;
  position: absolute;
  bottom: 0;
  left: -2em;
  transform: translateX(-50%);
